.NewTodoForm {
  margin-top: 3rem;
  display: flex;
  flex-wrap: wrap;
}

.NewTodoForm form {
  width: 100%;
  text-align: left;
  max-width: 425px;
  margin: auto;
}

.NewTodoForm form > div {
  display: flex;
}

.NewTodoForm label {
  min-width: 100%;
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
}

.NewTodoForm input {
  flex-grow: 1;
  border: none;
  background: #f7f1f1;
  padding: 0 1.5em;
  font-size: initial;
  outline: none;
  width: 100%;
}

.NewTodoForm button {
  padding: 0 1.3rem;
  border: none;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  border: 1px solid rgba(255, 255, 255, 0.3);
  margin-left: 5px;
  cursor: pointer;
  transition: background 0.4s ease-out;
  outline: none;
  min-width: 135px;
}

.NewTodoForm button:hover {
  background: rgba(0, 0, 0, 0.1);
}

.NewTodoForm input,
.NewTodoForm button {
  font-family: "Quicksand", sans-serif;
  height: 3rem;
}